@import url(./vendor/swiper.min.css);
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-gray: #858585;
  --quill-gray: #e5e5e3;
  --river-bed: #48555B;
  --color-cerulean: #00A4E5;
  --color-woodsmoke: #171819;
  --color-bunker: #141A20;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

