html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  font-size: 13px;
  color: var(--river-bed);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 940px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.me__button {
  position: relative;
  display: inline-block;
}

.me__button-btn {
  outline: none;
  padding: 10px 30px;
  text-transform: uppercase;
  color: var(--color-white);
  background: var(--color-cerulean);
  transition: all 0.3s ease;
}

.me__button-btn:hover {
  color: var(--color-cerulean);
  background: var(--color-white);
}

.me__button-btn:hover::before, .me__button-btn:hover::after {
  width: 100%;
  box-shadow: 0 4px 10px 2px rgba(0, 164, 229, 0.2);
  transition: 800ms ease all;
}

.me__button-btn::before, .me__button-btn::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 0;
  height: 3px;
  background: var(--color-cerulean);
  transition: all 0.3s ease;
}

.me__button-btn::after {
  left: 0;
  right: inherit;
  top: inherit;
  bottom: 0;
}

.button {
  display: none;
}

.button--show {
  display: block;
}

.button__up {
  position: fixed;
  right: 3%;
  bottom: 5%;
}

.button__btn {
  position: relative;
  border: 1px solid var(--color-cerulean);
  border-radius: 100%;
  padding: 10px;
  box-shadow: 0 4px 10px 2px rgba(0, 164, 229, 0.2);
  transition: all 0.3s ease;
}

.button__btn svg {
  width: 25px;
  height: 25px;
  fill: var(--color-cerulean);
}

.button__btn:hover {
  box-shadow: 0 4px 10px 2px rgba(0, 164, 229, 0.2);
  background: var(--color-cerulean);
  transition: all 0.3s ease;
}

.button__btn:hover svg {
  fill: var(--color-white);
  transition: all 0.3s ease;
}

@media (max-width: 576px) {
  .button {
    display: none;
  }
}

.konstruct {
  height: 100vh;
  min-height: 715px;
  background: url("../img/bg-intro.png") center no-repeat;
  background-size: cover;
}

.konstruct__container {
  position: relative;
}

.konstruct-head__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 43px;
}

.konstruct-head__logo svg {
  margin-right: 14px;
  width: 115px;
  height: 24px;
}

.konstruct-head__content {
  margin: 0 auto;
  padding: 25% 0;
  text-align: center;
  color: var(--color-white);
}

@media (max-width: 768px) {
  .konstruct-head__content {
    padding: 35% 0;
  }
}

.konstruct-head__content-title {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 60px;
  text-transform: uppercase;
}

@media (max-width: 768px) {
  .konstruct-head__content-title {
    font-size: 40px;
  }
}

@media (max-width: 576px) {
  .konstruct-head__content-title {
    font-size: 35px;
  }
}

.konstruct-head__content-subtitle {
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
}

@media (max-width: 768px) {
  .konstruct-head__content-subtitle {
    font-size: 11px;
  }
}

@media (max-width: 576px) {
  .konstruct-head__content-subtitle {
    font-size: 10px;
  }
}

@media (max-width: 768px) {
  .konstruct-head__content {
    padding: 10% 0;
  }
}

@media (max-width: 576px) {
  .konstruct-head__content {
    padding: 40% 0;
  }
}

.konstruct-head__button {
  position: absolute;
  left: 50%;
  bottom: -10%;
  text-align: center;
  transform: translateX(-50%);
}

.konstruct-head__button-btn {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
}

.konstruct-head__button span {
  font-weight: 600;
  font-size: 11px;
  line-height: 15px;
  letter-spacing: 4px;
  text-align: center;
  color: var(--color-white);
  background-image: linear-gradient(120deg, #00a4e5 0%, #00d4ff 100%);
  background-position: 0 50%;
  background-size: 100% 0.1em;
  background-repeat: no-repeat;
  transition: background-size 0.3s ease-in;
}

.konstruct-head__button span:hover {
  background-size: 100% 100%;
}

@media (max-width: 1200px) {
  .konstruct-head__button {
    bottom: -5%;
  }
}

@media (max-width: 768px) {
  .konstruct-head__button {
    bottom: -15%;
  }
}

@media (max-width: 576px) {
  .konstruct-head__button {
    bottom: -35%;
  }
}

@media (max-width: 1200px) {
  .konstruct {
    min-height: 680px;
  }
}

@media (max-width: 768px) {
  .konstruct {
    min-height: 360px;
  }
}

@media (max-width: 576px) {
  .konstruct {
    min-height: 500px;
  }
}

.header {
  border-bottom: 1px solid #e6e6e6;
}

.header-fixed {
  position: fixed;
  top: 0;
  z-index: 999;
  width: 100%;
  background: var(--color-white);
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 0;
}

@media (max-width: 1200px) {
  .header__container {
    padding: 15px;
  }
}

@media (max-width: 768px) {
  .header {
    position: relative;
  }
}

.logo {
  display: inline-block;
  width: 80px;
  height: 18px;
  background: var(--color-dark);
}

.logo svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-dark);
  object-fit: cover;
}

@media (max-width: 768px) {
  .logo {
    margin: 0 auto;
  }
}

.burger {
  position: relative;
  display: none;
  width: 25px;
  height: 15px;
}

.burger::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-bunker);
}

.burger::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-bunker);
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  background-color: var(--color-bunker);
  transform: translateY(-50%);
}

@media (max-width: 768px) {
  .burger {
    display: block;
  }
}

.nav__list {
  display: flex;
}

@media (max-width: 768px) {
  .nav__list {
    justify-content: center;
    width: 100%;
  }
}

.nav__item:not(:last-child) {
  margin-right: 40px;
}

@media (max-width: 1200px) {
  .nav__item:not(:last-child) {
    margin-right: 30px;
  }
}

@media (max-width: 576px) {
  .nav__item:not(:last-child) {
    margin-right: 20px;
  }
}

.nav__link {
  color: var(--river-bed);
}

.nav__link--current {
  color: var(--color-cerulean);
}

@media (max-width: 768px) {
  .nav {
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    left: 0;
    top: 20px;
    width: 100%;
    height: 48px;
    background-color: #fff;
    opacity: 0;
    transition: top 0.3s, opacity 0.3s;
  }
}

.nav--visible {
  top: 0;
  opacity: 1;
  transition: top 0.3s, opacity 0.3s;
}

.nav__close {
  display: none;
}

@media (max-width: 768px) {
  .nav__close {
    display: block;
  }
}

@media (max-width: 576px) {
  .nav__close {
    display: block;
  }
}

.main-link {
  font-weight: 600;
  font-size: 12px;
  transition: color 0.2s;
}

.main-link:hover {
  color: var(--color-cerulean);
  transition: color 0.2s;
}

.story {
  margin-bottom: 50px;
  padding: 50px 0;
}

.story-title {
  margin-bottom: 30px;
  text-align: center;
}

.story-title__one {
  position: relative;
  display: inline-block;
  font-weight: 700;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  color: var(--river-bed);
}

.story-title__one::before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -5px;
  display: block;
  width: 50%;
  height: 2px;
  background: var(--color-cerulean);
  transform: translateX(-50%);
}

@media (max-width: 768px) {
  .story {
    margin-bottom: 0;
  }
}

.box {
  display: flex;
}

.box-left {
  width: 435px;
}

.box-left__info-title {
  margin-bottom: 10px;
  padding-right: 40px;
  font-weight: 700;
  font-size: 20px;
  color: var(--river-bed);
}

.box-left__info-title span {
  color: var(--color-cerulean);
}

@media (max-width: 1200px) {
  .box-left__info-title {
    font-size: 15px;
  }
}

.box-left__info-descr {
  padding-right: 45px;
  line-height: 20px;
  color: #48555b;
}

.box-right {
  display: flex;
  padding-top: 10px;
}

@media (max-width: 768px) {
  .box-right {
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .box {
    flex-wrap: wrap;
  }
}

.card-grid {
  overflow: hidden;
}

.card-grid:not(:last-child) {
  margin-right: 20px;
}

@media (max-width: 576px) {
  .card-grid:not(:last-child) {
    margin-right: 10px;
  }
}

.card-grid__item {
  width: 140px;
  height: 224px;
  transition: all 0.3s ease-out;
}

.card-grid__item:hover {
  transform: scale(1.1);
  transition: all 0.3s ease-out;
}

@media (max-width: 576px) {
  .card-grid__item {
    width: 102px;
    height: 170px;
  }
}

.we {
  padding-top: 15px;
  padding-left: 15px;
}

.we__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  letter-spacing: 0.1875px;
  text-transform: uppercase;
