}

@media (max-width: 1200px) {
  .capab-head__title {
    width: 165px;
  }
}

@media (max-width: 768px) {
  .capab-head__title {
    width: 100px;
  }
}

@media (max-width: 576px) {
  .capab-head__title {
    width: 163px;
  }
}

@media (max-width: 576px) {
  .capab-head {
    margin: 0 auto 15px;
    width: 150px;
  }
}

.capab__descr {
  margin-left: 185px;
  width: 373px;
  font-size: 11px;
  line-height: 19px;
