.boxs {
  position: relative;
  margin-bottom: 50px;
}

@media (max-width: 768px) {
  .boxs {
    margin-bottom: 0;
  }
}

.box {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.box__item {
  margin: 0 10px;
  border: 1px solid #e6e6e6;
  padding: 35px 20px;
}

.box__link {
  color: var(--river-bed);
}

.box__img {
  display: block;
  margin-bottom: 20px;
}

.box__title {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--river-bed);
}

.box-prev-btn,
.box-next-btn {
  position: absolute;
  left: 15%;
  top: 50%;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--color-cerulean);
  border-radius: 100%;
  width: 25px;
  height: 25px;
  box-shadow: 0 4px 10px 2px rgba(0, 164, 229, 0.2);
  color: var(--color-cerulean);
  transform: translateY(-50%);
  transition: background-color 0.3s;
}

.box-prev-btn svg,
.box-next-btn svg {
  width: 40px;
  height: 20px;
  fill: currentColor;
  transition: fill 0.3s;
}

.box-prev-btn:hover,
.box-next-btn:hover {
  background: var(--color-cerulean);
  transition: background-color 0.3s;
}

.box-prev-btn:hover svg,
.box-next-btn:hover svg {
  fill: var(--color-white);
  transition: fill 0.3s;
}

@media (max-width: 1500px) {
  .box-prev-btn,
  .box-next-btn {
    left: 7%;
  }
}

@media (max-width: 1200px) {
  .box-prev-btn,
  .box-next-btn {
    top: 115%;
  }
}

@media (max-width: 768px) {
  .box-prev-btn,
  .box-next-btn {
    top: 115%;
  }
}

@media (max-width: 576px) {
  .box-prev-btn,
  .box-next-btn {
    left: 30%;
    top: 115%;
  }
}

.box-next-btn {
  left: auto;
  right: 17%;
}

@media (max-width: 1500px) {
  .box-next-btn {
    right: 7%;
  }
}

@media (max-width: 576px) {
  .box-next-btn {
    left: 60%;
  }
}

