@mixin desktop {
	@media (min-width: (1025px)) {
		@content;
	}
}

@mixin lg {
	@media (max-width: (1500px)) {
		@content;
	}
}

@mixin md {
	@media (max-width: (1200px)) {
		@content;
	}
}

@mixin sm {
	@media (max-width: (768px)) {
		@content;
	}
}

@mixin xs {
	@media (max-width: (576px)) {
		@content;
	}
}

@mixin mobile {
	@media (max-width: (1024px)) {
		@content;
	}
}
